main {
    width: 100%;
    float: left;
    margin-top: 60px;

    .banner {
        width: 100%;
        padding: 40px 20%;
        display: flex;
        justify-content: center;
        align-items: center;

        h1 {
            font-weight: 500;
            line-height: 1.8;
            font-size: 2rem;
            text-align: center;
            width: 85%;
            margin: 0 7.5%;
            color:#292929;
        }

        a {
            text-decoration: none;
            background-color: #000000;
            color: #ffffff;
        }
    }

    .breadcrum {
        width: 100%;
        margin-bottom: 20px;

        a {
            text-decoration: none;
            color: #000000;
            font-size: 1rem;
            display: inline-block;
            font-weight: 600;
        }

        img {
            font-size: 1.25rem;
            display: inline-block;
            position: relative;
            top: 7px;
        }

        span {
            text-decoration: none;
            color: #000000;
            font-size: 1rem;
            display: inline-block;
        }
    }

    .thnk {
        text-align: center;

        img {
            width: 120px;
            margin: 20px auto;
            display: block;
        }

        p {
            text-align: center;
        }
    }

    .hmbanner {
        width: 100%;
        display: flex;
        background-color: #f3f5ff;
        background-image: linear-gradient(to bottom, #ebffeb, #fff3f3);

        h1 {
            font-size: 3rem;
            text-align: center;
            margin: 1em 1em .2em;
        }

        p {
            text-align: center;
            font-size: 1.2rem;
            line-height: 2;
            padding: 0 5em;
            margin-bottom: 1em;
        }

        a {
            border: 1px solid #292929;
            color: #292929;
            text-decoration: none;
            display: inline-block;
            margin: 0 10px 10px;
            padding: 12px 33px;
            font-size: .875rem;
            font-weight: 600;


            &.fill {
                background-color: #292929;
                color: #ffffff;
            }

            &:hover {
                border: 2px solid #292929;
                padding: 11px 32px;
            }
        }



        img {
            width: 300px;
            margin: 0 auto;
            display: block;
        }

        .bgimgcont {
            position: relative;

            .imgt1,
            .imgt2,
            .imgt3,
            .imgt4 {
                position: absolute;
                filter: drop-shadow(22px -12px 7px rgba(0, 0, 0, 0.5));
                background-color: #ffffff;
                padding: 8px 20px;
                font-weight: 700;
                border-radius: 25px;
            }


            .imgt1 {
                bottom: 20%;
                left: 29%;
                filter: drop-shadow(22px -12px 7px rgba(0, 0, 0, 0.5));
                animation: leaf1 5s infinite alternate-reverse;
            }

            .imgt2 {
                bottom: 45%;
                left: 25%;
                filter: drop-shadow(22px -12px 7px rgba(0, 0, 0, 0.5));
                animation: leaf2 2s infinite alternate-reverse ease-in-out;
            }

            .imgt3 {
                bottom: 50%;
                right: 25%;
                filter: drop-shadow(-22px -12px 7px rgba(0, 0, 0, 0.5));
                animation: leaf3 8s infinite alternate-reverse ease-in-out;
            }

            .imgt4 {
                bottom: 25%;
                right: 25%;
                filter: drop-shadow(-22px -12px 7px rgba(0, 0, 0, 0.5));
                animation: leaf3 5s infinite alternate-reverse ease-in-out;
            }

            @keyframes leaf1 {
                from {
                    transform: translateY(20px);
                }

                to {
                    transform: translateY(0);
                }
            }

            @keyframes leaf2 {
                from {
                    transform: translateY(0);
                }

                to {
                    transform: translateY(10px);
                }
            }

            @keyframes leaf3 {
                from {
                    transform: translateY(30px);
                }

                to {
                    transform: translateY(0);
                }
            }

            @keyframes leaf4 {
                from {
                    transform: translateY(0px);
                }

                to {
                    transform: translateY(10px);
                }
            }

            .imgcnt {
                img {
                    position: absolute;
                    width: 45px;
                    opacity: .9;

                    &:nth-child(1) {
                        bottom: 10%;
                        left: -5%;
                        transform: rotate(10deg);
                        animation: ico1 5s infinite alternate-reverse ease-in-out;
                    }

                    &:nth-child(2) {
                        bottom: 28%;
                        left: 9%;
                        transform: rotate(-80deg);
                        animation: ico2 6s infinite alternate-reverse ease-in-out;
                    }

                    &:nth-child(3) {
                        bottom: 8%;
                        left: 14%;
                        transform: rotate(-30deg);
                        animation: ico3 3s infinite alternate-reverse ease-in-out;
                    }

                    &:nth-child(4) {
                        bottom: 35%;
                        left: 19%;
                        transform: rotate(-40deg);
                        animation: ico1 4s infinite alternate-reverse ease-in-out;
                    }

                    &:nth-child(5) {
                        bottom: 38%;
                        right: 19%;
                        transform: rotate(-40deg);
                        animation: ico2 2s infinite alternate-reverse ease-in-out;
                    }

                    &:nth-child(6) {
                        bottom: 8%;
                        right: 14%;
                        transform: rotate(-20deg);
                        animation: ico3 6s infinite alternate-reverse ease-in-out;
                    }


                    &:nth-child(7) {
                        bottom: 28%;
                        right: 9%;
                        transform: rotate(40deg);
                        animation: ico1 7s infinite alternate-reverse ease-in-out;
                    }


                    &:nth-child(8) {
                        bottom: 10%;
                        right: -5%;
                        transform: rotate(-20deg);
                        animation: ico2 3s infinite alternate-reverse ease-in-out;
                    }

                }

                @keyframes ico1 {
                    from {
                        transform: translateY(30px) rotate(-30deg);
                    }

                    to {
                        transform: translateY(0) rotate(-30deg);
                    }
                }

                @keyframes ico2 {
                    from {
                        transform: translateY(40px) rotate(40deg);
                    }

                    to {
                        transform: translateY(0) rotate(40deg);
                    }
                }

                @keyframes ico3 {
                    from {
                        transform: translateY(10px) rotate(-20deg);
                    }

                    to {
                        transform: translateY(0) rotate(-20deg);
                    }
                }

            }
        }
    }

    .hmwhwedo {
        padding: 4% 0;
        background-color: #ffffff;
    }

    .hmcrdtit {
        font-size: 2.25rem;
        font-weight: 700;
        text-align: center;
        margin: 0 0 1.5em;
        color:#292929
    }

    .hmcrd {
        border-radius: 4px;
        width: 90%;
        margin-left: 5%;
        margin-right: 5%;
        margin-bottom: 5%;
        padding: 8%;
        box-shadow: 10px 10px 10px rgb(46 54 68 / 3%);

        h2 {
            font-size: 1.125rem;
            font-weight: 500;
            margin-bottom: 8px;
