        }

        &.org {
            background-color: #fce8dd;
        }

        &.blu {
            background-color: #dcf7fe;
        }

        &.ylw {
            background-color: #fceac2;
        }

        &.grn {
            background-color: #c9fcc2;
        }

        &.mtp {
            margin-top: 2em;
        }
    }

    .hmserv {
        padding: 50px 0;
        background-color: #fff8f8;

        .serlogo {
            width: 100px;
            margin: 0 auto;

            img {
                width: 100%;
                border-radius: 50%;
                box-shadow: 10px 10px 10px rgb(46 54 68 / 3%);
            }
        }

        h2 {
            font-size: 1.4rem;
            font-weight: 700;
            margin: 25px 0;
            line-height: 2;
            text-align: center;
            color: #0c1320;
        }

        p {
            font-size: 0.875rem;
            margin: 0;
            color: #292929;
            letter-spacing: 0.2px;
            line-height: 1.9em;
            text-align: center;
            padding: 0 3em;
        }

    }

    .hmttcom {
        background-color: #ffe1e1;
        background-image: url('images/video-bg.png');
        background-size: cover;
        padding: 4% 0 5%;


        img {
            width: 90%;
            margin: 0 5%;
        }

        h2 {
            font-size: 1.5rem;
            color: #0c1320;
            text-align: left;
            padding: 2% 0 2%;
        }

        p {
            font-size: 0.875rem;
            color: #0c1320;
            line-height: 1.9em;
            text-align: left;
        }
    }


}

.gbtn {
    border: 1px solid #292929;
    color: #292929;
    text-decoration: none;
    padding: 10px 30px;
    font-size: 0.875rem;
    font-weight: 600;
    display: inline-block;


    &:hover {
        border: 2px solid #292929;
        padding: 9px 29px;
    }
}

@media only screen and (max-width: 600px) {
    main {

        .imgcnt,
        .imgt1,
        .imgt2,
        .imgt3,
        .imgt4 {
            display: none;
        }

        .hmttcom {
            h2 {
                font-size: 2rem;
            }
        }

        .hmbanner {
            h1 {
                font-size: 2rem;
            }
        }
    }
}

@media only screen and (max-width: 760px) {
    main {

        .imgcnt {
            display: none;
        }

        .imgt1 {
            left: 20% !important;
        }

        .imgt2 {
            left: 5% !important;
        }

        .imgt3 {
            right: 5% !important;
        }

        .imgt4 {
            right: 15% !important;
        }

        .hmcrd {
            &.mtp {
                margin-top: unset;
            }
        }
    }
}